import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { toast, ToastId } from 'react-toastify';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(<App />, document.getElementById('root'));

const refreshPage = (serviceWorker: ServiceWorker | null, toastId: ToastId) => (
    event: React.MouseEvent<HTMLButtonElement>
) => {
    event.preventDefault();
    toast.dismiss(toastId);
    if (serviceWorker) {
        serviceWorker.addEventListener('statechange', stateChangeEvent => {
            if (
                (stateChangeEvent.target as ServiceWorker).state === 'activated'
            ) {
                window.location.reload(true);
            }
        });
        serviceWorker.postMessage({ type: 'SKIP_WAITING' });
    }
};

const handleUpdate = (serviceWorker: ServiceWorker) => {
    let toastId: ToastId = 'updates-available';  

    toast.info(
        <>
            Updates are available;{' '}
            <button onClick={event => refreshPage(serviceWorker, toastId)(event)}>
                Click here to refresh and receive the updates.
            </button>
        </>,
        { toastId, autoClose: false, position: 'top-center', closeOnClick: false}
    );
};

const serviceWorkerConfig: serviceWorker.Config = {
    onUpdate: handleUpdate
}

serviceWorker.register(serviceWorkerConfig);
